'use client';

import { useState } from 'react';
import { motion } from 'framer-motion';
import {
  Github,
  Star,
  GitFork,
  Users,
  ExternalLink,
  RefreshCw,
  Unplug,
  Code2,
  BookMarked,
} from 'lucide-react';
import {
  PieChart,
  Pie,
  Cell,
  ResponsiveContainer,
  Tooltip,
} from 'recharts';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import type { GitHubProfile } from '@/types/profile';

interface GitHubStatsProps {
  github: GitHubProfile;
  isOwner: boolean;
  onSync?: () => Promise<void>;
  onConnect?: () => void;
  onDisconnect?: () => Promise<void>;
}

// Vibrant color palette for languages
const LANGUAGE_COLORS: Record<string, string> = {
  TypeScript: '#3178c6',
  JavaScript: '#f7df1e',
  Python: '#3572A5',
  Java: '#b07219',
  'C++': '#f34b7d',
  C: '#555555',
  Go: '#00ADD8',
  Rust: '#dea584',
  Ruby: '#cc342d',
  PHP: '#4F5D95',
  Swift: '#FA7343',
  Kotlin: '#A97BFF',
  Dart: '#00B4AB',
  HTML: '#e34c26',
  CSS: '#563d7c',
  Shell: '#89e051',
  Vue: '#41b883',
  Other: '#8b949e',
};

export function GitHubStats({
  github,
  isOwner,
  onSync,
  onConnect,
  onDisconnect,
}: GitHubStatsProps) {
  const [isSyncing, setIsSyncing] = useState(false);

  const handleSync = async () => {
    if (!onSync) return;
    setIsSyncing(true);
    try {
      await onSync();
    } finally {
      setIsSyncing(false);
    }
  };

  // Not connected state
  if (!github.connected) {
    return (
      <Card className="bg-white dark:bg-neutral-900 border border-gray-200 dark:border-gray-200 dark:border-neutral-800">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
            <Github className="w-5 h-5" />
            GitHub
          </h2>
        </div>
        <div className="text-center py-8">
          <div className="w-16 h-16 mx-auto mb-4 bg-gray-100 dark:bg-neutral-800 rounded-full flex items-center justify-center">
            <Github className="w-8 h-8 text-gray-500 dark:text-neutral-500" />
          </div>
          <p className="text-gray-600 dark:text-neutral-400 mb-4">
            {isOwner
              ? 'Connect your GitHub account to showcase your repositories and contributions.'
              : 'GitHub account not connected.'}
          </p>
          {isOwner && onConnect && (
            <Button
              onClick={onConnect}
              className="bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white"
            >
              <Github className="w-4 h-4 mr-2" />
              Connect GitHub
            </Button>
          )}
        </div>
      </Card>
    );
  }

  const stats = github.stats;
  if (!stats) {
    return (
      <Card className="bg-white dark:bg-neutral-900 border border-gray-200 dark:border-gray-200 dark:border-neutral-800">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
            <Github className="w-5 h-5" />
            GitHub
          </h2>
        </div>
        <p className="text-gray-600 dark:text-neutral-400">No GitHub stats available yet.</p>
      </Card>
    );
  }

  // Prepare language data for pie chart
  const languageData = Object.entries(stats.topLanguages || {})
    .map(([name, data]) => ({
      name,
      value: data.percentage,
      color: LANGUAGE_COLORS[name] || LANGUAGE_COLORS['Other'],
    }))
    .slice(0, 6);

  return (
    <Card className="bg-white dark:bg-neutral-900 border border-gray-200 dark:border-gray-200 dark:border-neutral-800">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
          <Github className="w-5 h-5" />
          GitHub
          {github.username && (
            <a
              href={github.profileUrl || `https://github.com/${github.username}`}
              target="_blank"
              rel="noopener noreferrer"
              className="text-sm text-gray-600 dark:text-neutral-400 hover:text-blue-400 flex items-center gap-1 ml-2"
            >
              @{github.username}
              <ExternalLink className="w-3 h-3" />
            </a>
          )}
        </h2>
        {isOwner && (
          <div className="flex items-center gap-2">
            <Button
              variant="ghost"
              size="icon"
              onClick={handleSync}
              disabled={isSyncing}
              className="text-gray-600 dark:text-neutral-400 hover:text-gray-900 dark:text-white"
              title="Sync GitHub stats"
            >
              <RefreshCw
                className={`w-4 h-4 ${isSyncing ? 'animate-spin' : ''}`}
              />
            </Button>
            {onDisconnect && (
              <Button
                variant="ghost"
                size="icon"
                onClick={onDisconnect}
                className="text-gray-600 dark:text-neutral-400 hover:text-red-400"
                title="Disconnect GitHub"
              >
                <Unplug className="w-4 h-4" />
              </Button>
            )}
          </div>
        )}
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
        <StatItem
          icon={<BookMarked className="w-5 h-5 text-blue-400" />}
          label="Repositories"
          value={stats.totalPublicRepos}
        />
        <StatItem
          icon={<Star className="w-5 h-5 text-amber-400" />}
          label="Total Stars"
          value={stats.totalStars}
        />
        <StatItem
          icon={<GitFork className="w-5 h-5 text-purple-400" />}
          label="Total Forks"
          value={stats.totalForks}
        />
        <StatItem
          icon={<Users className="w-5 h-5 text-emerald-400" />}
          label="Followers"
          value={stats.followers}
        />
      </div>

      {/* Languages Chart & Top Repos */}
      <div className="grid md:grid-cols-2 gap-6">
        {/* Language Breakdown */}
        {languageData.length > 0 && (
          <div>
            <h3 className="text-sm font-medium text-gray-600 dark:text-neutral-400 mb-3 flex items-center gap-2">
              <Code2 className="w-4 h-4" />
              Top Languages
            </h3>
            <div className="flex items-center gap-4">
              <div className="w-32 h-32">
                <ResponsiveContainer width="100%" height="100%">
                  <PieChart>
                    <Pie
                      data={languageData}
                      dataKey="value"
                      nameKey="name"
                      cx="50%"
                      cy="50%"
                      innerRadius={30}
                      outerRadius={50}
                      strokeWidth={2}
                      stroke="#171717"
                    >
                      {languageData.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={entry.color} />
                      ))}
                    </Pie>
                    <Tooltip
                      content={({ active, payload }) => {
                        if (active && payload && payload.length) {
                          return (
                            <div className="bg-gray-100 dark:bg-neutral-800 px-3 py-2 rounded-lg border border-gray-300 dark:border-neutral-700 shadow-lg">
                              <p className="text-gray-900 dark:text-white text-sm font-medium">
                                {payload[0].name}
                              </p>
                              <p className="text-gray-600 dark:text-neutral-400 text-xs">
                                {payload[0].value?.toFixed(1)}%
                              </p>
                            </div>
                          );
                        }
                        return null;
                      }}
                    />
                  </PieChart>
                </ResponsiveContainer>
              </div>
              <div className="flex-1 space-y-2">
                {languageData.slice(0, 5).map((lang) => (
                  <div key={lang.name} className="flex items-center gap-2">
                    <div
                      className="w-3 h-3 rounded-full"
                      style={{ backgroundColor: lang.color }}
                    />
                    <span className="text-sm text-gray-700 dark:text-neutral-300 flex-1">
                      {lang.name}
                    </span>
                    <span className="text-sm text-gray-500 dark:text-neutral-500">
                      {lang.value.toFixed(1)}%
                    </span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        {/* Top Repos */}
        {stats.topRepos && stats.topRepos.length > 0 && (
          <div>
            <h3 className="text-sm font-medium text-gray-600 dark:text-neutral-400 mb-3 flex items-center gap-2">
              <Star className="w-4 h-4" />
              Top Repositories
            </h3>
            <div className="space-y-3">
              {stats.topRepos.slice(0, 3).map((repo, index) => (
                <motion.a
                  key={repo.name}
                  href={repo.url}
                  target="_blank"
                  rel="noopener noreferrer"
                  initial={{ opacity: 0, x: -10 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ delay: index * 0.1 }}
                  className="block p-3 bg-gray-100 dark:bg-neutral-800/50 rounded-lg border border-gray-300 dark:border-neutral-700/50 hover:border-neutral-600 transition-colors"
                >
                  <div className="flex items-start justify-between">
                    <div className="flex-1 min-w-0">
                      <h4 className="text-gray-900 dark:text-white font-medium truncate flex items-center gap-2">
                        <BookMarked className="w-4 h-4 text-gray-500 dark:text-neutral-500 flex-shrink-0" />
                        {repo.name}
                      </h4>
                      {repo.description && (
                        <p className="text-gray-600 dark:text-neutral-400 text-sm mt-1 line-clamp-1">
                          {repo.description}
                        </p>
                      )}
                    </div>
                    <ExternalLink className="w-4 h-4 text-gray-500 dark:text-neutral-500 flex-shrink-0 ml-2" />
                  </div>
                  <div className="flex items-center gap-3 mt-2 text-xs text-gray-500 dark:text-neutral-500">
                    {repo.language && (
                      <span className="flex items-center gap-1">
                        <span
                          className="w-2 h-2 rounded-full"
                          style={{
                            backgroundColor:
                              LANGUAGE_COLORS[repo.language] ||
                              LANGUAGE_COLORS['Other'],
                          }}
                        />
                        {repo.language}
                      </span>
                    )}
                    <span className="flex items-center gap-1">
                      <Star className="w-3 h-3" />
                      {repo.stars}
                    </span>
                    <span className="flex items-center gap-1">
                      <GitFork className="w-3 h-3" />
                      {repo.forks}
                    </span>
                  </div>
                </motion.a>
              ))}
            </div>
          </div>
        )}
      </div>

      {/* Last Synced */}
      {github.lastSyncedAt && (
        <p className="text-xs text-gray-500 dark:text-neutral-500 mt-4 text-right">
          Last synced:{' '}
          {new Date(github.lastSyncedAt).toLocaleDateString('en-US', {
            month: 'short',
            day: 'numeric',
            hour: '2-digit',
            minute: '2-digit',
          })}
        </p>
      )}
    </Card>
  );
}

interface StatItemProps {
  icon: React.ReactNode;
  label: string;
  value: number;
}

function StatItem({ icon, label, value }: StatItemProps) {
  return (
    <div className="p-3 bg-gray-100 dark:bg-neutral-800/50 rounded-lg border border-gray-300 dark:border-neutral-700/50">
      <div className="flex items-center gap-2 mb-1">{icon}</div>
      <p className="text-xl font-bold text-gray-900 dark:text-white">{value.toLocaleString()}</p>
      <p className="text-xs text-gray-600 dark:text-neutral-400">{label}</p>
    </div>
  );
}

