'use client';

import { useState } from 'react';
import { motion } from 'framer-motion';
import {
  ChevronDown,
  ChevronUp,
  Calendar,
  Activity,
  TrendingUp,
  BookOpen,
  MessageSquare,
  Heart,
} from 'lucide-react';
import { Card } from '@/components/ui/Card';
import type { ProfileUser, ProfileStats } from '@/types/profile';

interface ProfileAboutProps {
  user: ProfileUser;
  stats: ProfileStats;
}

export function ProfileAbout({ user, stats }: ProfileAboutProps) {
  const [expanded, setExpanded] = useState(false);
  const bioPreviewLength = 200;

  const shouldShowExpandButton =
    user.bio && user.bio.length > bioPreviewLength;
  const displayBio = expanded
    ? user.bio
    : user.bio?.slice(0, bioPreviewLength) + (shouldShowExpandButton ? '...' : '');

  return (
    <Card className="border border-gray-200 dark:border-neutral-800">
      <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4 flex items-center gap-2">
        <BookOpen className="w-5 h-5 text-blue-600 dark:text-blue-400" />
        About
      </h2>

      {/* Bio */}
      {user.bio ? (
        <div className="mb-6">
          <p className="text-gray-700 dark:text-neutral-300 leading-relaxed whitespace-pre-wrap">
            {displayBio}
          </p>
          {shouldShowExpandButton && (
            <button
              onClick={() => setExpanded(!expanded)}
              className="mt-2 text-blue-600 hover:text-blue-700 dark:text-blue-400 dark:hover:text-blue-300 text-sm font-medium flex items-center gap-1"
            >
              {expanded ? (
                <>
                  Show less <ChevronUp className="w-4 h-4" />
                </>
              ) : (
                <>
                  Show more <ChevronDown className="w-4 h-4" />
                </>
              )}
            </button>
          )}
        </div>
      ) : (
        <p className="text-gray-500 dark:text-neutral-500 italic mb-6">No bio added yet.</p>
      )}

      {/* Interests */}
      {user.interests && user.interests.length > 0 && (
        <div className="mb-6">
          <h3 className="text-sm font-medium text-gray-600 dark:text-neutral-400 mb-2">
            Interests
          </h3>
          <div className="flex flex-wrap gap-2">
            {user.interests.map((interest, index) => (
              <motion.span
                key={interest}
                initial={{ scale: 0.9, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                transition={{ delay: index * 0.05 }}
                className="px-3 py-1 bg-gray-100 dark:bg-neutral-800 text-gray-700 dark:text-neutral-300 rounded-full text-sm border border-gray-300 dark:border-neutral-700"
              >
                {interest}
              </motion.span>
            ))}
          </div>
        </div>
      )}

      {/* Quick Stats */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <StatCard
          icon={<Activity className="w-5 h-5 text-emerald-400" />}
          label="Active Days"
          value={stats.totalActiveDays}
          color="emerald"
        />
        <StatCard
          icon={<MessageSquare className="w-5 h-5 text-blue-400" />}
          label="Posts"
          value={stats.totalPosts}
          color="blue"
        />
        <StatCard
          icon={<TrendingUp className="w-5 h-5 text-purple-400" />}
          label="Articles"
          value={stats.totalArticles}
          color="purple"
        />
        <StatCard
          icon={<Heart className="w-5 h-5 text-rose-400" />}
          label="Likes Received"
          value={stats.totalLikesReceived}
          color="rose"
        />
      </div>

      {/* Education Info */}
      {(user.college || user.degree) && (
        <div className="mt-6 pt-6 border-t border-gray-200 dark:border-neutral-800">
          <h3 className="text-sm font-medium text-gray-600 dark:text-neutral-400 mb-3">
            Education
          </h3>
          <div className="flex items-start gap-3">
            <div className="w-10 h-10 bg-gray-100 dark:bg-neutral-800 rounded-lg flex items-center justify-center">
              <Calendar className="w-5 h-5 text-gray-600 dark:text-neutral-400" />
            </div>
            <div>
              <p className="text-gray-900 dark:text-white font-medium">
                {user.degree || 'Student'}
                {user.branch && ` in ${user.branch}`}
              </p>
              <p className="text-gray-600 dark:text-neutral-400 text-sm">{user.college}</p>
              {(user.currentYear || user.graduationYear) && (
                <p className="text-gray-500 dark:text-neutral-500 text-sm mt-0.5">
                  {user.currentYear && `Year ${user.currentYear}`}
                  {user.currentYear && user.graduationYear && ' • '}
                  {user.graduationYear && `Graduating ${user.graduationYear}`}
                </p>
              )}
            </div>
          </div>
        </div>
      )}
    </Card>
  );
}

interface StatCardProps {
  icon: React.ReactNode;
  label: string;
  value: number;
  color: 'emerald' | 'blue' | 'purple' | 'rose';
}

function StatCard({ icon, label, value, color }: StatCardProps) {
  const bgColors = {
    emerald: 'bg-emerald-500/10 border-emerald-500/20',
    blue: 'bg-blue-500/10 border-blue-500/20',
    purple: 'bg-purple-500/10 border-purple-500/20',
    rose: 'bg-rose-500/10 border-rose-500/20',
  };

  return (
    <motion.div
      whileHover={{ scale: 1.02 }}
      className={`p-4 rounded-lg border ${bgColors[color]} transition-colors`}
    >
      <div className="flex items-center gap-2 mb-2">{icon}</div>
      <p className="text-2xl font-bold text-gray-900 dark:text-white">{value.toLocaleString()}</p>
      <p className="text-sm text-gray-600 dark:text-neutral-400">{label}</p>
    </motion.div>
  );
}

