'use client';

import { motion } from 'framer-motion';
import {
  FolderGit2,
  ExternalLink,
  Github,
  Star,
  Plus,
  Calendar,
  ChevronRight,
} from 'lucide-react';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import type { Project } from '@/types/profile';

interface ProjectsGridProps {
  projects: Project[];
  isOwner: boolean;
  onAddProject?: () => void;
  onToggleFeatured?: (id: string) => void;
  onEditProject?: (project: Project) => void;
}

export function ProjectsGrid({
  projects,
  isOwner,
  onAddProject,
  onToggleFeatured,
  onEditProject,
}: ProjectsGridProps) {
  // Separate featured and regular projects
  const featuredProjects = projects.filter((p) => p.featured);
  const regularProjects = projects.filter((p) => !p.featured);

  if (projects.length === 0 && !isOwner) {
    return null;
  }

  return (
    <Card className="bg-white dark:bg-neutral-900 border border-gray-200 dark:border-gray-200 dark:border-neutral-800">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
          <FolderGit2 className="w-5 h-5 text-purple-400" />
          Projects
          <span className="text-sm font-normal text-gray-600 dark:text-neutral-400 ml-2">
            {projects.length} projects
          </span>
        </h2>

        {isOwner && onAddProject && (
          <Button
            onClick={onAddProject}
            className="bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white text-sm"
          >
            <Plus className="w-4 h-4 mr-1" />
            Add
          </Button>
        )}
      </div>

      {projects.length === 0 ? (
        <div className="text-center py-8">
          <div className="w-16 h-16 mx-auto mb-4 bg-gray-100 dark:bg-neutral-800 rounded-full flex items-center justify-center">
            <FolderGit2 className="w-8 h-8 text-gray-500 dark:text-neutral-500" />
          </div>
          <p className="text-gray-600 dark:text-neutral-400">No projects added yet.</p>
          {isOwner && onAddProject && (
            <Button
              onClick={onAddProject}
              className="mt-4 bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white"
            >
              <Plus className="w-4 h-4 mr-2" />
              Add your first project
            </Button>
          )}
        </div>
      ) : (
        <div className="space-y-6">
          {/* Featured Projects */}
          {featuredProjects.length > 0 && (
            <div>
              <h3 className="text-sm font-medium text-amber-400 mb-3 flex items-center gap-2">
                <Star className="w-4 h-4" />
                Featured
              </h3>
              <div className="grid md:grid-cols-2 gap-4">
                {featuredProjects.map((project, index) => (
                  <ProjectCard
                    key={project.id}
                    project={project}
                    isOwner={isOwner}
                    onToggleFeatured={onToggleFeatured}
                    onEdit={onEditProject}
                    index={index}
                    featured
                  />
                ))}
              </div>
            </div>
          )}

          {/* Regular Projects */}
          {regularProjects.length > 0 && (
            <div>
              {featuredProjects.length > 0 && (
                <h3 className="text-sm font-medium text-gray-600 dark:text-neutral-400 mb-3">
                  All Projects
                </h3>
              )}
              <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4">
                {regularProjects.map((project, index) => (
                  <ProjectCard
                    key={project.id}
                    project={project}
                    isOwner={isOwner}
                    onToggleFeatured={onToggleFeatured}
                    onEdit={onEditProject}
                    index={index}
                  />
                ))}
              </div>
            </div>
          )}
        </div>
      )}
    </Card>
  );
}

interface ProjectCardProps {
  project: Project;
  isOwner: boolean;
  onToggleFeatured?: (id: string) => void;
  onEdit?: (project: Project) => void;
  index: number;
  featured?: boolean;
}

function ProjectCard({
  project,
  isOwner,
  onToggleFeatured,
  onEdit,
  index,
  featured,
}: ProjectCardProps) {
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short',
      year: 'numeric',
    });
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: index * 0.1 }}
      className={`group relative bg-gray-100 dark:bg-neutral-800/50 rounded-lg border overflow-hidden transition-all hover:border-neutral-600 ${
        featured
          ? 'border-amber-500/30 hover:border-amber-500/50'
          : 'border-gray-300 dark:border-neutral-700/50'
      }`}
    >
      {/* Project Image */}
      {project.images && project.images.length > 0 ? (
        <div className="aspect-video bg-gray-100 dark:bg-neutral-800 overflow-hidden">
          <img
            src={project.images[0]}
            alt={project.name}
            className="w-full h-full object-cover transition-transform group-hover:scale-105"
          />
        </div>
      ) : (
        <div className="aspect-video bg-gradient-to-br from-neutral-800 to-neutral-900 flex items-center justify-center">
          <FolderGit2 className="w-12 h-12 text-neutral-600" />
        </div>
      )}

      {/* Featured Badge */}
      {featured && (
        <div className="absolute top-3 left-3 px-2 py-1 bg-amber-500/20 border border-amber-500/30 rounded-full text-amber-400 text-xs flex items-center gap-1">
          <Star className="w-3 h-3" />
          Featured
        </div>
      )}

      {/* Content */}
      <div className="p-4">
        <h4 className="text-gray-900 dark:text-white font-semibold mb-1 group-hover:text-blue-400 transition-colors">
          {project.name}
        </h4>

        {project.role && (
          <p className="text-sm text-gray-600 dark:text-neutral-400 mb-2">{project.role}</p>
        )}

        <p className="text-gray-600 dark:text-neutral-400 text-sm line-clamp-2 mb-3">
          {project.description}
        </p>

        {/* Tech Stack */}
        {project.techStack && project.techStack.length > 0 && (
          <div className="flex flex-wrap gap-1 mb-3">
            {project.techStack.slice(0, 4).map((tech) => (
              <span
                key={tech}
                className="px-2 py-0.5 bg-neutral-700/50 text-gray-700 dark:text-neutral-300 rounded text-xs"
              >
                {tech}
              </span>
            ))}
            {project.techStack.length > 4 && (
              <span className="px-2 py-0.5 text-gray-500 dark:text-neutral-500 text-xs">
                +{project.techStack.length - 4}
              </span>
            )}
          </div>
        )}

        {/* Date */}
        <div className="flex items-center gap-1 text-xs text-gray-500 dark:text-neutral-500 mb-3">
          <Calendar className="w-3 h-3" />
          {formatDate(project.startDate)}
          {project.endDate ? ` - ${formatDate(project.endDate)}` : ' - Present'}
        </div>

        {/* Links */}
        <div className="flex items-center gap-2">
          {project.projectUrl && (
            <a
              href={project.projectUrl}
              target="_blank"
              rel="noopener noreferrer"
              className="inline-flex items-center gap-1 text-sm text-blue-400 hover:text-blue-300"
            >
              <ExternalLink className="w-4 h-4" />
              Demo
            </a>
          )}
          {project.githubUrl && (
            <a
              href={project.githubUrl}
              target="_blank"
              rel="noopener noreferrer"
              className="inline-flex items-center gap-1 text-sm text-gray-600 dark:text-neutral-400 hover:text-gray-900 dark:text-white"
            >
              <Github className="w-4 h-4" />
              Code
            </a>
          )}
        </div>

        {/* Owner Actions */}
        {isOwner && (
          <div className="absolute top-3 right-3 flex items-center gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
            {onToggleFeatured && (
              <button
                onClick={() => onToggleFeatured(project.id)}
                className={`p-1.5 rounded-lg transition-colors ${
                  project.featured
                    ? 'bg-amber-500/20 text-amber-400'
                    : 'bg-neutral-700 text-gray-600 dark:text-neutral-400 hover:text-amber-400'
                }`}
                title={project.featured ? 'Remove from featured' : 'Add to featured'}
              >
                <Star className="w-4 h-4" />
              </button>
            )}
            {onEdit && (
              <button
                onClick={() => onEdit(project)}
                className="p-1.5 bg-neutral-700 text-gray-600 dark:text-neutral-400 rounded-lg hover:text-gray-900 dark:text-white transition-colors"
                title="Edit project"
              >
                <ChevronRight className="w-4 h-4" />
              </button>
            )}
          </div>
        )}
      </div>
    </motion.div>
  );
}

