'use client';

import { useState, useMemo } from 'react';
import { motion } from 'framer-motion';
import { Search, Plus, X, Sparkles, Code, Palette, Wrench, Users } from 'lucide-react';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import type { UserSkill } from '@/types/profile';

interface SkillsGridProps {
  skills: UserSkill[];
  isOwner: boolean;
  onAddSkill?: () => void;
  onRemoveSkill?: (id: string) => void;
}

// Category icons
const CATEGORY_ICONS: Record<string, React.ReactNode> = {
  Programming: <Code className="w-4 h-4" />,
  Design: <Palette className="w-4 h-4" />,
  Tools: <Wrench className="w-4 h-4" />,
  'Soft Skills': <Users className="w-4 h-4" />,
  default: <Sparkles className="w-4 h-4" />,
};

// Proficiency colors
const PROFICIENCY_COLORS: Record<string, string> = {
  Beginner: 'bg-blue-500/20 text-blue-400 border-blue-500/30',
  Intermediate: 'bg-emerald-500/20 text-emerald-400 border-emerald-500/30',
  Advanced: 'bg-purple-500/20 text-purple-400 border-purple-500/30',
  Expert: 'bg-amber-500/20 text-amber-400 border-amber-500/30',
};

export function SkillsGrid({
  skills,
  isOwner,
  onAddSkill,
  onRemoveSkill,
}: SkillsGridProps) {
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);

  // Get unique categories
  const categories = useMemo(() => {
    const cats = new Set(
      skills.map((s) => s.skill.category || 'Other').filter(Boolean)
    );
    return Array.from(cats);
  }, [skills]);

  // Filter skills
  const filteredSkills = useMemo(() => {
    return skills.filter((s) => {
      const matchesSearch = s.skill.name
        .toLowerCase()
        .includes(searchQuery.toLowerCase());
      const matchesCategory =
        !selectedCategory ||
        (s.skill.category || 'Other') === selectedCategory;
      return matchesSearch && matchesCategory;
    });
  }, [skills, searchQuery, selectedCategory]);

  // Group by category
  const groupedSkills = useMemo(() => {
    const groups: Record<string, UserSkill[]> = {};
    filteredSkills.forEach((skill) => {
      const category = skill.skill.category || 'Other';
      if (!groups[category]) {
        groups[category] = [];
      }
      groups[category].push(skill);
    });
    return groups;
  }, [filteredSkills]);

  if (skills.length === 0 && !isOwner) {
    return null;
  }

  return (
    <Card className="bg-white dark:bg-neutral-900 border border-gray-200 dark:border-gray-200 dark:border-neutral-800">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4 mb-6">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
          <Sparkles className="w-5 h-5 text-amber-400" />
          Skills
          <span className="text-sm font-normal text-gray-600 dark:text-neutral-400 ml-2">
            {skills.length} skills
          </span>
        </h2>

        <div className="flex items-center gap-3">
          {/* Search */}
          {skills.length > 5 && (
            <div className="relative">
              <Search className="w-4 h-4 absolute left-3 top-1/2 -translate-y-1/2 text-gray-500 dark:text-neutral-500" />
              <input
                type="text"
                placeholder="Search skills..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="bg-gray-100 dark:bg-neutral-800 text-gray-700 dark:text-neutral-300 text-sm pl-9 pr-3 py-1.5 rounded-lg border border-gray-300 dark:border-neutral-700 focus:outline-none focus:border-neutral-600 w-40"
              />
            </div>
          )}

          {/* Add Skill */}
          {isOwner && onAddSkill && (
            <Button
              onClick={onAddSkill}
              className="bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white text-sm"
            >
              <Plus className="w-4 h-4 mr-1" />
              Add
            </Button>
          )}
        </div>
      </div>

      {/* Category Filters */}
      {categories.length > 1 && (
        <div className="flex flex-wrap gap-2 mb-4">
          <button
            onClick={() => setSelectedCategory(null)}
            className={`px-3 py-1 rounded-full text-sm transition-colors ${
              !selectedCategory
                ? 'bg-blue-500/20 text-blue-400 border border-blue-500/30'
                : 'bg-gray-100 dark:bg-neutral-800 text-gray-600 dark:text-neutral-400 border border-gray-300 dark:border-neutral-700 hover:border-neutral-600'
            }`}
          >
            All
          </button>
          {categories.map((category) => (
            <button
              key={category}
              onClick={() => setSelectedCategory(category)}
              className={`px-3 py-1 rounded-full text-sm flex items-center gap-1 transition-colors ${
                selectedCategory === category
                  ? 'bg-blue-500/20 text-blue-400 border border-blue-500/30'
                  : 'bg-gray-100 dark:bg-neutral-800 text-gray-600 dark:text-neutral-400 border border-gray-300 dark:border-neutral-700 hover:border-neutral-600'
              }`}
            >
              {CATEGORY_ICONS[category] || CATEGORY_ICONS.default}
              {category}
            </button>
          ))}
        </div>
      )}

      {/* Skills Grid */}
      {skills.length === 0 ? (
        <div className="text-center py-8">
          <div className="w-16 h-16 mx-auto mb-4 bg-gray-100 dark:bg-neutral-800 rounded-full flex items-center justify-center">
            <Sparkles className="w-8 h-8 text-gray-500 dark:text-neutral-500" />
          </div>
          <p className="text-gray-600 dark:text-neutral-400">No skills added yet.</p>
          {isOwner && onAddSkill && (
            <Button
              onClick={onAddSkill}
              className="mt-4 bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white"
            >
              <Plus className="w-4 h-4 mr-2" />
              Add your first skill
            </Button>
          )}
        </div>
      ) : filteredSkills.length === 0 ? (
        <p className="text-gray-600 dark:text-neutral-400 text-center py-4">
          No skills match your search.
        </p>
      ) : (
        <div className="space-y-6">
          {Object.entries(groupedSkills).map(([category, categorySkills]) => (
            <div key={category}>
              <h3 className="text-sm font-medium text-gray-600 dark:text-neutral-400 mb-3 flex items-center gap-2">
                {CATEGORY_ICONS[category] || CATEGORY_ICONS.default}
                {category}
              </h3>
              <div className="flex flex-wrap gap-2">
                {categorySkills.map((userSkill, index) => (
                  <motion.div
                    key={userSkill.id}
                    initial={{ scale: 0.9, opacity: 0 }}
                    animate={{ scale: 1, opacity: 1 }}
                    transition={{ delay: index * 0.03 }}
                    className={`group relative inline-flex items-center gap-2 px-3 py-1.5 rounded-lg border transition-colors ${
                      userSkill.proficiency
                        ? PROFICIENCY_COLORS[userSkill.proficiency]
                        : 'bg-gray-100 dark:bg-neutral-800 text-gray-700 dark:text-neutral-300 border-gray-300 dark:border-neutral-700'
                    }`}
                  >
                    <span className="text-sm font-medium">
                      {userSkill.skill.name}
                    </span>
                    {userSkill.yearsOfExp && (
                      <span className="text-xs opacity-70">
                        {userSkill.yearsOfExp}y
                      </span>
                    )}
                    {isOwner && onRemoveSkill && (
                      <button
                        onClick={() => onRemoveSkill(userSkill.id)}
                        className="opacity-0 group-hover:opacity-100 transition-opacity ml-1"
                      >
                        <X className="w-3 h-3" />
                      </button>
                    )}
                  </motion.div>
                ))}
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Legend */}
      {skills.some((s) => s.proficiency) && (
        <div className="flex flex-wrap items-center gap-3 mt-6 pt-4 border-t border-gray-200 dark:border-neutral-800 text-xs text-gray-500 dark:text-neutral-500">
          <span>Proficiency:</span>
          {Object.entries(PROFICIENCY_COLORS).map(([level, classes]) => (
            <span
              key={level}
              className={`px-2 py-0.5 rounded border ${classes}`}
            >
              {level}
            </span>
          ))}
        </div>
      )}
    </Card>
  );
}

