'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import {
  GraduationCap,
  Plus,
  Calendar,
  ChevronDown,
  ChevronUp,
  Edit2,
  Award,
  BookOpen,
} from 'lucide-react';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import type { Education } from '@/types/profile';

interface EducationTimelineProps {
  education: Education[];
  isOwner: boolean;
  onAddEducation?: () => void;
  onEditEducation?: (education: Education) => void;
}

export function EducationTimeline({
  education,
  isOwner,
  onAddEducation,
  onEditEducation,
}: EducationTimelineProps) {
  const [expandedId, setExpandedId] = useState<string | null>(null);

  if (education.length === 0 && !isOwner) {
    return null;
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short',
      year: 'numeric',
    });
  };

  return (
    <Card className="bg-white dark:bg-neutral-900 border border-gray-200 dark:border-gray-200 dark:border-neutral-800">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
          <GraduationCap className="w-5 h-5 text-emerald-400" />
          Education
          <span className="text-sm font-normal text-gray-600 dark:text-neutral-400 ml-2">
            {education.length} entries
          </span>
        </h2>

        {isOwner && onAddEducation && (
          <Button
            onClick={onAddEducation}
            className="bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white text-sm"
          >
            <Plus className="w-4 h-4 mr-1" />
            Add
          </Button>
        )}
      </div>

      {education.length === 0 ? (
        <div className="text-center py-8">
          <div className="w-16 h-16 mx-auto mb-4 bg-gray-100 dark:bg-neutral-800 rounded-full flex items-center justify-center">
            <GraduationCap className="w-8 h-8 text-gray-500 dark:text-neutral-500" />
          </div>
          <p className="text-gray-600 dark:text-neutral-400">No education added yet.</p>
          {isOwner && onAddEducation && (
            <Button
              onClick={onAddEducation}
              className="mt-4 bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white"
            >
              <Plus className="w-4 h-4 mr-2" />
              Add your education
            </Button>
          )}
        </div>
      ) : (
        <div className="relative">
          {/* Timeline Line */}
          <div className="absolute left-5 top-0 bottom-0 w-px bg-neutral-700" />

          {/* Timeline Items */}
          <div className="space-y-6">
            {education.map((edu, index) => (
              <motion.div
                key={edu.id}
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: index * 0.1 }}
                className="relative pl-12"
              >
                {/* Timeline Dot */}
                <div
                  className={`absolute left-3 top-1 w-5 h-5 rounded-full border-2 ${
                    edu.isCurrent
                      ? 'bg-emerald-500 border-emerald-400'
                      : 'bg-gray-100 dark:bg-neutral-800 border-neutral-600'
                  }`}
                >
                  {edu.isCurrent && (
                    <span className="absolute inset-0 animate-ping bg-emerald-500 rounded-full opacity-50" />
                  )}
                </div>

                {/* Card */}
                <div className="group bg-gray-100 dark:bg-neutral-800/50 rounded-lg border border-gray-300 dark:border-neutral-700/50 p-4 hover:border-neutral-600 transition-colors">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      {/* School & Degree */}
                      <div className="flex items-start gap-3">
                        <div className="w-10 h-10 bg-neutral-700 rounded-lg flex items-center justify-center flex-shrink-0">
                          <BookOpen className="w-5 h-5 text-gray-600 dark:text-neutral-400" />
                        </div>
                        <div>
                          <h4 className="text-gray-900 dark:text-white font-semibold">
                            {edu.school}
                          </h4>
                          <p className="text-gray-700 dark:text-neutral-300">
                            {edu.degree}
                            {edu.fieldOfStudy && ` in ${edu.fieldOfStudy}`}
                          </p>
                        </div>
                      </div>

                      {/* Meta Info */}
                      <div className="flex flex-wrap items-center gap-3 mt-3 text-sm">
                        <span className="flex items-center gap-1 text-gray-600 dark:text-neutral-400">
                          <Calendar className="w-3 h-3" />
                          {formatDate(edu.startDate)} -{' '}
                          {edu.isCurrent
                            ? 'Present'
                            : edu.endDate
                            ? formatDate(edu.endDate)
                            : 'N/A'}
                        </span>
                        {edu.grade && (
                          <span className="flex items-center gap-1 text-emerald-400">
                            <Award className="w-3 h-3" />
                            {edu.grade}
                          </span>
                        )}
                      </div>

                      {/* Activities */}
                      {edu.activities && (
                        <div className="mt-3 text-sm text-gray-600 dark:text-neutral-400">
                          <span className="font-medium text-gray-700 dark:text-neutral-300">
                            Activities:{' '}
                          </span>
                          {edu.activities}
                        </div>
                      )}

                      {/* Description (Expandable) */}
                      {edu.description && (
                        <div className="mt-3">
                          <button
                            onClick={() =>
                              setExpandedId(
                                expandedId === edu.id ? null : edu.id
                              )
                            }
                            className="text-blue-400 hover:text-blue-300 text-sm flex items-center gap-1"
                          >
                            {expandedId === edu.id ? (
                              <>
                                Hide details <ChevronUp className="w-4 h-4" />
                              </>
                            ) : (
                              <>
                                Show details <ChevronDown className="w-4 h-4" />
                              </>
                            )}
                          </button>
                          <AnimatePresence>
                            {expandedId === edu.id && (
                              <motion.div
                                initial={{ height: 0, opacity: 0 }}
                                animate={{ height: 'auto', opacity: 1 }}
                                exit={{ height: 0, opacity: 0 }}
                                transition={{ duration: 0.2 }}
                                className="overflow-hidden"
                              >
                                <p className="text-gray-600 dark:text-neutral-400 text-sm mt-2 whitespace-pre-wrap">
                                  {edu.description}
                                </p>
                              </motion.div>
                            )}
                          </AnimatePresence>
                        </div>
                      )}
                    </div>

                    {/* Edit Button */}
                    {isOwner && onEditEducation && (
                      <button
                        onClick={() => onEditEducation(edu)}
                        className="opacity-0 group-hover:opacity-100 p-2 text-gray-600 dark:text-neutral-400 hover:text-gray-900 dark:text-white transition-all"
                      >
                        <Edit2 className="w-4 h-4" />
                      </button>
                    )}
                  </div>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      )}
    </Card>
  );
}

