'use client';

import { useState } from 'react';
import { motion } from 'framer-motion';
import {
  MapPin,
  Linkedin,
  Github,
  Globe,
  Edit2,
  UserPlus,
  MessageCircle,
  Zap,
  Flame,
  Shield,
  CheckCircle2,
  Share2,
  MoreHorizontal,
  Users,
  FileText,
  Eye,
  Calendar,
  Briefcase,
  GraduationCap,
  Camera,
} from 'lucide-react';
import { Button } from '@/components/ui/Button';
import type { ProfileUser, ProfileStats } from '@/types/profile';

interface ProfileHeaderProps {
  user: ProfileUser;
  stats: ProfileStats;
  isOwner: boolean;
  onEditProfile?: () => void;
  onEditBanner?: () => void;
  onEditAvatar?: () => void;
}

export function ProfileHeader({
  user,
  stats,
  isOwner,
  onEditProfile,
  onEditBanner,
  onEditAvatar,
}: ProfileHeaderProps) {
  const [showShareMenu, setShowShareMenu] = useState(false);
  const [imageLoaded, setImageLoaded] = useState(false);

  // Format numbers for display
  const formatNumber = (num: number): string => {
    if (num >= 1000000) return `${(num / 1000000).toFixed(1)}M`;
    if (num >= 1000) return `${(num / 1000).toFixed(1)}K`;
    return num.toString();
  };

  // Copy profile URL
  const handleShareProfile = () => {
    const url = `${window.location.origin}/profile/${user.id}`;
    navigator.clipboard.writeText(url);
    setShowShareMenu(false);
  };

  return (
    <div className="relative">
      {/* ════════════════════════════════════════════════════════════════════════
          BANNER SECTION - Full Width with Gradient Overlay
      ════════════════════════════════════════════════════════════════════════ */}
      <div className="relative h-56 sm:h-72 md:h-80 lg:h-96 w-full overflow-hidden">
        {/* Banner Image or Gradient */}
        {user.bannerImageUrl ? (
          <>
            <motion.img
              initial={{ scale: 1.1, opacity: 0 }}
              animate={{ scale: 1, opacity: imageLoaded ? 1 : 0 }}
              transition={{ duration: 0.6 }}
              src={user.bannerImageUrl}
              alt="Profile banner"
              className="w-full h-full object-cover"
              onLoad={() => setImageLoaded(true)}
            />
            {/* Shimmer loading effect */}
            {!imageLoaded && (
              <div className="absolute inset-0 bg-gradient-to-r from-neutral-800 via-neutral-700 to-neutral-800 animate-pulse" />
            )}
          </>
        ) : (
          /* Default gradient banner with animated mesh */
          <div className="w-full h-full relative overflow-hidden">
            <div className="absolute inset-0 bg-gradient-to-br from-indigo-600 via-purple-600 to-pink-500" />
            {/* Animated floating shapes */}
            <div className="absolute inset-0 overflow-hidden">
              <motion.div
                animate={{ 
                  x: [0, 100, 0], 
                  y: [0, -50, 0],
                  scale: [1, 1.2, 1],
                }}
                transition={{ duration: 20, repeat: Infinity, ease: "linear" }}
                className="absolute -top-20 -left-20 w-96 h-96 bg-white/10 rounded-full blur-3xl"
              />
              <motion.div
                animate={{ 
                  x: [0, -80, 0], 
                  y: [0, 60, 0],
                  scale: [1, 0.8, 1],
                }}
                transition={{ duration: 15, repeat: Infinity, ease: "linear" }}
                className="absolute -bottom-20 -right-20 w-80 h-80 bg-pink-500/20 rounded-full blur-3xl"
              />
              <motion.div
                animate={{ 
                  x: [0, 50, 0], 
                  y: [0, -30, 0],
                }}
                transition={{ duration: 18, repeat: Infinity, ease: "linear" }}
                className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-64 h-64 bg-blue-500/20 rounded-full blur-3xl"
              />
            </div>
          </div>
        )}

        {/* Gradient overlay for text readability */}
        <div className="absolute inset-0 bg-gradient-to-t from-black/70 via-black/20 to-transparent" />

        {/* Edit banner button (owner only) */}
        {isOwner && (
          <motion.button
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.5 }}
            onClick={onEditBanner}
            className="absolute top-4 right-4 p-2.5 rounded-full bg-black/40 backdrop-blur-md border border-white/20 text-white hover:bg-black/60 transition-all group"
          >
            <Camera className="w-5 h-5 group-hover:scale-110 transition-transform" />
          </motion.button>
        )}
      </div>

      {/* ════════════════════════════════════════════════════════════════════════
          GLASSMORPHISM PROFILE CARD - Overlapping Banner
      ════════════════════════════════════════════════════════════════════════ */}
      <div className="relative max-w-5xl mx-auto px-4 sm:px-6 lg:px-8 -mt-32 sm:-mt-36 md:-mt-40">
        <motion.div
          initial={{ y: 50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: 0.5, delay: 0.2 }}
          className="relative rounded-2xl overflow-hidden"
        >
          {/* Glassmorphism background */}
          <div className="absolute inset-0 bg-white/80 dark:bg-neutral-900/80 backdrop-blur-xl border border-white/20 dark:border-neutral-700/50 rounded-2xl" />
          
          {/* Card content */}
          <div className="relative p-6 sm:p-8">
            <div className="flex flex-col lg:flex-row gap-6">
              {/* ─────────────────────────────────────────────────────────────────
                  PROFILE AVATAR - Large, overlapping style
              ───────────────────────────────────────────────────────────────── */}
              <div className="flex-shrink-0 -mt-20 sm:-mt-24 lg:-mt-28 relative mx-auto lg:mx-0">
                <motion.div
                  initial={{ scale: 0.8, opacity: 0 }}
                  animate={{ scale: 1, opacity: 1 }}
                  transition={{ duration: 0.4, delay: 0.3 }}
                  className="relative"
                >
                  {/* Avatar ring glow effect */}
                  <div className="absolute inset-0 rounded-full bg-gradient-to-br from-indigo-500 via-purple-500 to-pink-500 blur-lg opacity-50 scale-105" />
                  
                  {/* Avatar container */}
                  <div className="relative w-32 h-32 sm:w-40 sm:h-40 lg:w-44 lg:h-44 rounded-full border-4 border-white dark:border-neutral-900 bg-white dark:bg-neutral-900 overflow-hidden shadow-2xl">
                    {user.avatar ? (
                      <img
                        src={user.avatar}
                        alt={user.name}
                        className="w-full h-full object-cover"
                      />
                    ) : (
                      <div className="w-full h-full bg-gradient-to-br from-indigo-500 via-purple-500 to-pink-500 flex items-center justify-center">
                        <span className="text-5xl sm:text-6xl font-bold text-white">
                          {user.name?.charAt(0)?.toUpperCase() || '?'}
                        </span>
                      </div>
                    )}
                  </div>

                  {/* Verified badge */}
                  {user.verified && (
                    <motion.div
                      initial={{ scale: 0 }}
                      animate={{ scale: 1 }}
                      transition={{ delay: 0.5 }}
                      className="absolute -bottom-1 -right-1 bg-blue-500 rounded-full p-2 border-[3px] border-white dark:border-neutral-900 shadow-lg"
                    >
                      <CheckCircle2 className="w-5 h-5 text-white" />
                    </motion.div>
                  )}

                  {/* Edit avatar button (owner only) */}
                  {isOwner && (
                    <button
                      onClick={onEditAvatar}
                      className="absolute bottom-2 left-2 p-2 rounded-full bg-white dark:bg-neutral-800 shadow-lg border border-gray-200 dark:border-neutral-700 text-gray-600 dark:text-neutral-300 hover:bg-gray-50 dark:hover:bg-neutral-700 transition-colors"
                    >
                      <Camera className="w-4 h-4" />
                    </button>
                  )}
                </motion.div>
              </div>

              {/* ─────────────────────────────────────────────────────────────────
                  USER INFO SECTION
              ───────────────────────────────────────────────────────────────── */}
              <div className="flex-1 text-center lg:text-left mt-2 lg:mt-0">
                {/* Name and badges row */}
                <div className="flex flex-col lg:flex-row lg:items-start lg:justify-between gap-4">
                  <div>
                    {/* Name with Open to Work badge */}
                    <motion.div
                      initial={{ y: 10, opacity: 0 }}
                      animate={{ y: 0, opacity: 1 }}
                      transition={{ delay: 0.35 }}
                      className="flex flex-wrap items-center justify-center lg:justify-start gap-3"
                    >
                      <h1 className="text-2xl sm:text-3xl lg:text-4xl font-bold text-gray-900 dark:text-white">
                        {user.name}
                      </h1>
                      {user.isOpenToOpportunities && (
                        <span className="inline-flex items-center px-3 py-1 rounded-full text-xs font-semibold bg-gradient-to-r from-emerald-500/20 to-teal-500/20 text-emerald-600 dark:text-emerald-400 border border-emerald-500/30 backdrop-blur-sm">
                          <Zap className="w-3 h-3 mr-1" />
                          Open to Work
                        </span>
                      )}
                    </motion.div>

                    {/* Headline */}
                    {user.headline && (
                      <motion.p
                        initial={{ y: 10, opacity: 0 }}
                        animate={{ y: 0, opacity: 1 }}
                        transition={{ delay: 0.4 }}
                        className="text-base sm:text-lg text-gray-600 dark:text-neutral-300 mt-2 max-w-xl"
                      >
                        {user.headline}
                      </motion.p>
                    )}

                    {/* Location and info pills */}
                    <motion.div
                      initial={{ y: 10, opacity: 0 }}
                      animate={{ y: 0, opacity: 1 }}
                      transition={{ delay: 0.45 }}
                      className="flex flex-wrap items-center justify-center lg:justify-start gap-2 mt-3"
                    >
                      {user.location && (
                        <span className="inline-flex items-center gap-1.5 px-3 py-1.5 rounded-full text-sm bg-gray-100 dark:bg-neutral-800 text-gray-600 dark:text-neutral-400">
                          <MapPin className="w-3.5 h-3.5" />
                          {user.location}
                        </span>
                      )}
                      {user.college && (
                        <span className="inline-flex items-center gap-1.5 px-3 py-1.5 rounded-full text-sm bg-gray-100 dark:bg-neutral-800 text-gray-600 dark:text-neutral-400">
                          <GraduationCap className="w-3.5 h-3.5" />
                          {user.college}
                        </span>
                      )}
                      {user.branch && (
                        <span className="inline-flex items-center gap-1.5 px-3 py-1.5 rounded-full text-sm bg-gray-100 dark:bg-neutral-800 text-gray-600 dark:text-neutral-400">
                          <Briefcase className="w-3.5 h-3.5" />
                          {user.branch}
                        </span>
                      )}
                    </motion.div>

                    {/* Social links */}
                    <motion.div
                      initial={{ y: 10, opacity: 0 }}
                      animate={{ y: 0, opacity: 1 }}
                      transition={{ delay: 0.5 }}
                      className="flex items-center justify-center lg:justify-start gap-3 mt-4"
                    >
                      {user.linkedinUrl && (
                        <a
                          href={user.linkedinUrl}
                          target="_blank"
                          rel="noopener noreferrer"
                          className="p-2.5 rounded-full bg-[#0A66C2]/10 text-[#0A66C2] hover:bg-[#0A66C2]/20 transition-colors"
                        >
                          <Linkedin className="w-5 h-5" />
                        </a>
                      )}
                      {user.githubProfileUrl && (
                        <a
                          href={user.githubProfileUrl}
                          target="_blank"
                          rel="noopener noreferrer"
                          className="p-2.5 rounded-full bg-gray-100 dark:bg-neutral-800 text-gray-700 dark:text-white hover:bg-gray-200 dark:hover:bg-neutral-700 transition-colors"
                        >
                          <Github className="w-5 h-5" />
                        </a>
                      )}
                      {user.portfolioUrl && (
                        <a
                          href={user.portfolioUrl}
                          target="_blank"
                          rel="noopener noreferrer"
                          className="p-2.5 rounded-full bg-emerald-500/10 text-emerald-600 dark:text-emerald-400 hover:bg-emerald-500/20 transition-colors"
                        >
                          <Globe className="w-5 h-5" />
                        </a>
                      )}
                    </motion.div>
                  </div>

                  {/* ───────────────────────────────────────────────────────────────
                      ACTION BUTTONS & GAMIFICATION
                  ─────────────────────────────────────────────────────────────── */}
                  <motion.div
                    initial={{ y: 10, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{ delay: 0.55 }}
                    className="flex flex-col items-center lg:items-end gap-4"
                  >
                    {/* Gamification badges */}
                    <div className="flex items-center gap-2">
                      {/* Streak badge */}
                      <div className="flex items-center gap-2 px-3 py-2 rounded-xl bg-gradient-to-r from-amber-500/10 to-orange-500/10 border border-amber-500/20">
                        <Flame className="w-4 h-4 text-orange-500" />
                        <span className="text-sm font-semibold text-orange-600 dark:text-orange-400">
                          {stats.currentStreak} day streak
                        </span>
                      </div>
                      {/* Level badge */}
                      <div className="flex items-center gap-2 px-3 py-2 rounded-xl bg-gradient-to-r from-purple-500/10 to-indigo-500/10 border border-purple-500/20">
                        <Zap className="w-4 h-4 text-purple-500" />
                        <span className="text-sm font-semibold text-purple-600 dark:text-purple-400">
                          Level {stats.level}
                        </span>
                      </div>
                    </div>

                    {/* XP Progress bar */}
                    <div className="w-full max-w-[200px]">
                      <div className="flex justify-between text-xs text-gray-500 dark:text-neutral-400 mb-1">
                        <span>{stats.xp} XP</span>
                        <span>{stats.xpToNextLevel} to level up</span>
                      </div>
                      <div className="h-2 bg-gray-200 dark:bg-neutral-800 rounded-full overflow-hidden">
                        <motion.div
                          initial={{ width: 0 }}
                          animate={{ width: `${Math.min((stats.xp / (stats.xp + stats.xpToNextLevel)) * 100, 100)}%` }}
                          transition={{ duration: 0.8, delay: 0.6 }}
                          className="h-full bg-gradient-to-r from-purple-500 to-indigo-500 rounded-full"
                        />
                      </div>
                    </div>

                    {/* Action buttons */}
                    <div className="flex items-center gap-2">
                      {isOwner ? (
                        <>
                          <Button
                            onClick={onEditProfile}
                            className="flex items-center gap-2 px-5 py-2.5 bg-gray-900 dark:bg-white text-white dark:text-gray-900 hover:bg-gray-800 dark:hover:bg-gray-100 rounded-xl font-medium transition-all shadow-lg shadow-gray-900/20 dark:shadow-white/20"
                          >
                            <Edit2 className="w-4 h-4" />
                            Edit Profile
                          </Button>
                          <div className="relative">
                            <button
                              onClick={() => setShowShareMenu(!showShareMenu)}
                              className="p-2.5 rounded-xl bg-gray-100 dark:bg-neutral-800 text-gray-600 dark:text-neutral-300 hover:bg-gray-200 dark:hover:bg-neutral-700 transition-colors"
                            >
                              <Share2 className="w-5 h-5" />
                            </button>
                            {showShareMenu && (
                              <motion.div
                                initial={{ opacity: 0, scale: 0.95, y: -10 }}
                                animate={{ opacity: 1, scale: 1, y: 0 }}
                                className="absolute right-0 mt-2 w-48 py-2 bg-white dark:bg-neutral-800 rounded-xl shadow-xl border border-gray-200 dark:border-neutral-700 z-50"
                              >
                                <button
                                  onClick={handleShareProfile}
                                  className="w-full px-4 py-2 text-left text-sm text-gray-700 dark:text-neutral-300 hover:bg-gray-100 dark:hover:bg-neutral-700"
                                >
                                  Copy profile link
                                </button>
                              </motion.div>
                            )}
                          </div>
                        </>
                      ) : (
                        <>
                          <Button className="flex items-center gap-2 px-5 py-2.5 bg-blue-600 hover:bg-blue-700 text-white rounded-xl font-medium transition-all shadow-lg shadow-blue-600/30">
                            <UserPlus className="w-4 h-4" />
                            Connect
                          </Button>
                          <Button className="flex items-center gap-2 px-5 py-2.5 bg-gray-100 dark:bg-neutral-800 text-gray-700 dark:text-neutral-300 hover:bg-gray-200 dark:hover:bg-neutral-700 rounded-xl font-medium transition-all">
                            <MessageCircle className="w-4 h-4" />
                            Message
                          </Button>
                          <button className="p-2.5 rounded-xl bg-gray-100 dark:bg-neutral-800 text-gray-600 dark:text-neutral-300 hover:bg-gray-200 dark:hover:bg-neutral-700 transition-colors">
                            <MoreHorizontal className="w-5 h-5" />
                          </button>
                        </>
                      )}
                    </div>
                  </motion.div>
                </div>
              </div>
            </div>
          </div>
        </motion.div>

        {/* ════════════════════════════════════════════════════════════════════════
            STATS BAR - Below the main card
        ════════════════════════════════════════════════════════════════════════ */}
        <motion.div
          initial={{ y: 30, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ delay: 0.6 }}
          className="mt-4 grid grid-cols-2 sm:grid-cols-4 lg:grid-cols-6 gap-3"
        >
          <StatCard
            icon={<Users className="w-5 h-5" />}
            label="Connections"
            value={formatNumber(stats.connectionsCount)}
            color="blue"
          />
          <StatCard
            icon={<Users className="w-5 h-5" />}
            label="Followers"
            value={formatNumber(stats.followersCount)}
            color="purple"
          />
          <StatCard
            icon={<FileText className="w-5 h-5" />}
            label="Posts"
            value={formatNumber(stats.totalPosts)}
            color="emerald"
          />
          <StatCard
            icon={<FileText className="w-5 h-5" />}
            label="Articles"
            value={formatNumber(stats.totalArticles)}
            color="amber"
          />
          <StatCard
            icon={<Eye className="w-5 h-5" />}
            label="Likes Received"
            value={formatNumber(stats.totalLikesReceived)}
            color="pink"
          />
          <StatCard
            icon={<Calendar className="w-5 h-5" />}
            label="Active Days"
            value={formatNumber(stats.totalActiveDays)}
            color="indigo"
          />
        </motion.div>

        {/* Privacy Indicator */}
        {user.profileVisibility !== 'PUBLIC' && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.7 }}
            className="mt-4 inline-flex items-center gap-2 px-4 py-2 bg-amber-500/10 border border-amber-500/30 rounded-xl text-amber-600 dark:text-amber-400 text-sm"
          >
            <Shield className="w-4 h-4" />
            {user.profileVisibility === 'STUDENTS_ONLY'
              ? 'Visible to verified students only'
              : 'Visible to connections only'}
          </motion.div>
        )}
      </div>
    </div>
  );
}

/* ════════════════════════════════════════════════════════════════════════════════
   STAT CARD COMPONENT
════════════════════════════════════════════════════════════════════════════════ */
interface StatCardProps {
  icon: React.ReactNode;
  label: string;
  value: string;
  color: 'blue' | 'purple' | 'emerald' | 'amber' | 'pink' | 'indigo';
}

function StatCard({ icon, label, value, color }: StatCardProps) {
  const colorClasses = {
    blue: 'bg-blue-500/10 text-blue-600 dark:text-blue-400 border-blue-500/20',
    purple: 'bg-purple-500/10 text-purple-600 dark:text-purple-400 border-purple-500/20',
    emerald: 'bg-emerald-500/10 text-emerald-600 dark:text-emerald-400 border-emerald-500/20',
    amber: 'bg-amber-500/10 text-amber-600 dark:text-amber-400 border-amber-500/20',
    pink: 'bg-pink-500/10 text-pink-600 dark:text-pink-400 border-pink-500/20',
    indigo: 'bg-indigo-500/10 text-indigo-600 dark:text-indigo-400 border-indigo-500/20',
  };

  return (
    <motion.div
      whileHover={{ scale: 1.02, y: -2 }}
      className={`relative overflow-hidden rounded-xl border backdrop-blur-sm p-4 ${colorClasses[color]} transition-all cursor-pointer`}
    >
      <div className="flex items-center gap-3">
        <div className="flex-shrink-0">
          {icon}
        </div>
        <div>
          <p className="text-xl font-bold">{value}</p>
          <p className="text-xs opacity-70">{label}</p>
        </div>
      </div>
    </motion.div>
  );
}
