'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import {
  Briefcase,
  Plus,
  MapPin,
  Calendar,
  ChevronDown,
  ChevronUp,
  Edit2,
  Building2,
} from 'lucide-react';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import type { Experience } from '@/types/profile';

interface ExperienceTimelineProps {
  experiences: Experience[];
  isOwner: boolean;
  onAddExperience?: () => void;
  onEditExperience?: (experience: Experience) => void;
}

// Experience type colors
const TYPE_COLORS: Record<string, string> = {
  'Full-time': 'bg-blue-500/20 text-blue-400 border-blue-500/30',
  'Part-time': 'bg-purple-500/20 text-purple-400 border-purple-500/30',
  Internship: 'bg-emerald-500/20 text-emerald-400 border-emerald-500/30',
  Freelance: 'bg-amber-500/20 text-amber-400 border-amber-500/30',
  Contract: 'bg-rose-500/20 text-rose-400 border-rose-500/30',
};

export function ExperienceTimeline({
  experiences,
  isOwner,
  onAddExperience,
  onEditExperience,
}: ExperienceTimelineProps) {
  const [expandedId, setExpandedId] = useState<string | null>(null);

  if (experiences.length === 0 && !isOwner) {
    return null;
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short',
      year: 'numeric',
    });
  };

  const calculateDuration = (start: string, end: string | null) => {
    const startDate = new Date(start);
    const endDate = end ? new Date(end) : new Date();
    const months =
      (endDate.getFullYear() - startDate.getFullYear()) * 12 +
      (endDate.getMonth() - startDate.getMonth());
    const years = Math.floor(months / 12);
    const remainingMonths = months % 12;

    if (years > 0 && remainingMonths > 0) {
      return `${years} yr ${remainingMonths} mo`;
    } else if (years > 0) {
      return `${years} yr${years > 1 ? 's' : ''}`;
    } else {
      return `${remainingMonths} mo`;
    }
  };

  return (
    <Card className="bg-white dark:bg-neutral-900 border border-gray-200 dark:border-gray-200 dark:border-neutral-800">
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-white flex items-center gap-2">
          <Briefcase className="w-5 h-5 text-blue-400" />
          Experience
          <span className="text-sm font-normal text-gray-600 dark:text-neutral-400 ml-2">
            {experiences.length} positions
          </span>
        </h2>

        {isOwner && onAddExperience && (
          <Button
            onClick={onAddExperience}
            className="bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white text-sm"
          >
            <Plus className="w-4 h-4 mr-1" />
            Add
          </Button>
        )}
      </div>

      {experiences.length === 0 ? (
        <div className="text-center py-8">
          <div className="w-16 h-16 mx-auto mb-4 bg-gray-100 dark:bg-neutral-800 rounded-full flex items-center justify-center">
            <Briefcase className="w-8 h-8 text-gray-500 dark:text-neutral-500" />
          </div>
          <p className="text-gray-600 dark:text-neutral-400">No experience added yet.</p>
          {isOwner && onAddExperience && (
            <Button
              onClick={onAddExperience}
              className="mt-4 bg-gray-100 dark:bg-neutral-800 hover:bg-gray-200 dark:hover:bg-neutral-700 text-gray-900 dark:text-white"
            >
              <Plus className="w-4 h-4 mr-2" />
              Add your first experience
            </Button>
          )}
        </div>
      ) : (
        <div className="relative">
          {/* Timeline Line */}
          <div className="absolute left-5 top-0 bottom-0 w-px bg-neutral-700" />

          {/* Timeline Items */}
          <div className="space-y-6">
            {experiences.map((experience, index) => (
              <motion.div
                key={experience.id}
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: index * 0.1 }}
                className="relative pl-12"
              >
                {/* Timeline Dot */}
                <div
                  className={`absolute left-3 top-1 w-5 h-5 rounded-full border-2 ${
                    experience.isCurrent
                      ? 'bg-emerald-500 border-emerald-400'
                      : 'bg-gray-100 dark:bg-neutral-800 border-neutral-600'
                  }`}
                >
                  {experience.isCurrent && (
                    <span className="absolute inset-0 animate-ping bg-emerald-500 rounded-full opacity-50" />
                  )}
                </div>

                {/* Card */}
                <div className="group bg-gray-100 dark:bg-neutral-800/50 rounded-lg border border-gray-300 dark:border-neutral-700/50 p-4 hover:border-neutral-600 transition-colors">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      {/* Title & Company */}
                      <div className="flex items-start gap-3">
                        <div className="w-10 h-10 bg-neutral-700 rounded-lg flex items-center justify-center flex-shrink-0">
                          <Building2 className="w-5 h-5 text-gray-600 dark:text-neutral-400" />
                        </div>
                        <div>
                          <h4 className="text-gray-900 dark:text-white font-semibold">
                            {experience.title}
                          </h4>
                          <p className="text-gray-700 dark:text-neutral-300">{experience.company}</p>
                        </div>
                      </div>

                      {/* Meta Info */}
                      <div className="flex flex-wrap items-center gap-3 mt-3 text-sm">
                        <span
                          className={`px-2 py-0.5 rounded-full border text-xs ${
                            TYPE_COLORS[experience.type] ||
                            'bg-neutral-700 text-gray-700 dark:text-neutral-300 border-neutral-600'
                          }`}
                        >
                          {experience.type}
                        </span>
                        {experience.location && (
                          <span className="flex items-center gap-1 text-gray-600 dark:text-neutral-400">
                            <MapPin className="w-3 h-3" />
                            {experience.location}
                          </span>
                        )}
                        <span className="flex items-center gap-1 text-gray-600 dark:text-neutral-400">
                          <Calendar className="w-3 h-3" />
                          {formatDate(experience.startDate)} -{' '}
                          {experience.isCurrent
                            ? 'Present'
                            : experience.endDate
                            ? formatDate(experience.endDate)
                            : 'N/A'}
                        </span>
                        <span className="text-gray-500 dark:text-neutral-500">
                          ({calculateDuration(experience.startDate, experience.endDate)})
                        </span>
                      </div>

                      {/* Skills */}
                      {experience.skills && experience.skills.length > 0 && (
                        <div className="flex flex-wrap gap-1 mt-3">
                          {experience.skills.slice(0, 5).map((skill) => (
                            <span
                              key={skill}
                              className="px-2 py-0.5 bg-neutral-700/50 text-gray-700 dark:text-neutral-300 rounded text-xs"
                            >
                              {skill}
                            </span>
                          ))}
                          {experience.skills.length > 5 && (
                            <span className="text-gray-500 dark:text-neutral-500 text-xs">
                              +{experience.skills.length - 5} more
                            </span>
                          )}
                        </div>
                      )}

                      {/* Description (Expandable) */}
                      {experience.description && (
                        <div className="mt-3">
                          <button
                            onClick={() =>
                              setExpandedId(
                                expandedId === experience.id ? null : experience.id
                              )
                            }
                            className="text-blue-400 hover:text-blue-300 text-sm flex items-center gap-1"
                          >
                            {expandedId === experience.id ? (
                              <>
                                Hide details <ChevronUp className="w-4 h-4" />
                              </>
                            ) : (
                              <>
                                Show details <ChevronDown className="w-4 h-4" />
                              </>
                            )}
                          </button>
                          <AnimatePresence>
                            {expandedId === experience.id && (
                              <motion.div
                                initial={{ height: 0, opacity: 0 }}
                                animate={{ height: 'auto', opacity: 1 }}
                                exit={{ height: 0, opacity: 0 }}
                                transition={{ duration: 0.2 }}
                                className="overflow-hidden"
                              >
                                <p className="text-gray-600 dark:text-neutral-400 text-sm mt-2 whitespace-pre-wrap">
                                  {experience.description}
                                </p>
                              </motion.div>
                            )}
                          </AnimatePresence>
                        </div>
                      )}
                    </div>

                    {/* Edit Button */}
                    {isOwner && onEditExperience && (
                      <button
                        onClick={() => onEditExperience(experience)}
                        className="opacity-0 group-hover:opacity-100 p-2 text-gray-600 dark:text-neutral-400 hover:text-gray-900 dark:text-white transition-all"
                      >
                        <Edit2 className="w-4 h-4" />
                      </button>
                    )}
                  </div>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      )}
    </Card>
  );
}

