'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import * as Dialog from '@radix-ui/react-dialog';
import {
  X,
  Folder,
  Github,
  Globe,
  Calendar,
  Code2,
  Briefcase,
  Link2,
  Image as ImageIcon,
  Loader2,
  Plus,
  Trash2,
  Star,
  ExternalLink,
} from 'lucide-react';
import { Button } from '@/components/ui/Button';
import { createProject } from '@/lib/api/profile';
import type { ProjectInput, Project } from '@/types/profile';

interface AddProjectModalProps {
  isOpen: boolean;
  onClose: () => void;
  onProjectAdded: (project: Project) => void;
}

export function AddProjectModal({
  isOpen,
  onClose,
  onProjectAdded,
}: AddProjectModalProps) {
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [techInput, setTechInput] = useState('');

  const [formData, setFormData] = useState<ProjectInput>({
    name: '',
    description: '',
    role: '',
    techStack: [],
    startDate: '',
    endDate: '',
    isCurrent: false,
    projectUrl: '',
    githubUrl: '',
    images: [],
    featured: false,
  });

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleCheckboxChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, checked } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: checked,
      // Clear end date when marked as current
      ...(name === 'isCurrent' && checked ? { endDate: '' } : {}),
    }));
  };

  const addTech = () => {
    if (techInput.trim() && !formData.techStack?.includes(techInput.trim())) {
      setFormData((prev) => ({
        ...prev,
        techStack: [...(prev.techStack || []), techInput.trim()],
      }));
      setTechInput('');
    }
  };

  const removeTech = (tech: string) => {
    setFormData((prev) => ({
      ...prev,
      techStack: prev.techStack?.filter((t) => t !== tech) || [],
    }));
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      addTech();
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!formData.name.trim()) {
      setError('Project name is required');
      return;
    }
    if (!formData.description.trim()) {
      setError('Project description is required');
      return;
    }
    if (!formData.startDate) {
      setError('Start date is required');
      return;
    }

    setSaving(true);
    setError(null);

    try {
      const project = await createProject(formData);
      onProjectAdded(project);
      // Reset form
      setFormData({
        name: '',
        description: '',
        role: '',
        techStack: [],
        startDate: '',
        endDate: '',
        isCurrent: false,
        projectUrl: '',
        githubUrl: '',
        images: [],
        featured: false,
      });
      onClose();
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to add project');
    } finally {
      setSaving(false);
    }
  };

  return (
    <Dialog.Root open={isOpen} onOpenChange={onClose}>
      <Dialog.Portal>
        <AnimatePresence>
          {isOpen && (
            <>
              {/* Backdrop with blur */}
              <Dialog.Overlay asChild>
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  exit={{ opacity: 0 }}
                  className="fixed inset-0 bg-black/60 backdrop-blur-md z-50"
                />
              </Dialog.Overlay>

              {/* Modal Content */}
              <Dialog.Content asChild>
                <motion.div
                  initial={{ opacity: 0, scale: 0.95, y: 20 }}
                  animate={{ opacity: 1, scale: 1, y: 0 }}
                  exit={{ opacity: 0, scale: 0.95, y: 20 }}
                  transition={{ type: 'spring', damping: 25, stiffness: 300 }}
                  className="fixed top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-full max-w-2xl max-h-[90vh] z-50"
                >
                  {/* Glassmorphism container */}
                  <div className="relative bg-white/90 dark:bg-neutral-900/90 backdrop-blur-xl rounded-2xl border border-white/20 dark:border-neutral-700/50 shadow-2xl overflow-hidden">
                    {/* Gradient accent */}
                    <div className="absolute top-0 left-0 right-0 h-1 bg-gradient-to-r from-violet-500 via-fuchsia-500 to-pink-500" />

                    {/* Header */}
                    <div className="flex items-center justify-between p-5 border-b border-gray-200/50 dark:border-neutral-800/50">
                      <div className="flex items-center gap-3">
                        <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-violet-500 to-fuchsia-600 flex items-center justify-center shadow-lg">
                          <Folder className="w-5 h-5 text-white" />
                        </div>
                        <Dialog.Title className="text-xl font-bold text-gray-900 dark:text-white">
                          Add New Project
                        </Dialog.Title>
                      </div>
                      <Dialog.Close asChild>
                        <button className="p-2 text-gray-500 dark:text-neutral-400 hover:text-gray-700 dark:hover:text-white transition-colors rounded-lg hover:bg-gray-100 dark:hover:bg-neutral-800">
                          <X className="w-5 h-5" />
                        </button>
                      </Dialog.Close>
                    </div>

                    {/* Form Content */}
                    <form onSubmit={handleSubmit} className="overflow-y-auto max-h-[calc(90vh-160px)]">
                      <div className="p-5 space-y-5">
                        {/* Error Display */}
                        {error && (
                          <motion.div
                            initial={{ opacity: 0, y: -10 }}
                            animate={{ opacity: 1, y: 0 }}
                            className="p-3 bg-red-500/10 border border-red-500/20 rounded-xl text-red-600 dark:text-red-400 text-sm"
                          >
                            {error}
                          </motion.div>
                        )}

                        {/* Project Name */}
                        <div className="space-y-2">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <Code2 className="w-4 h-4 text-violet-500" />
                            Project Name *
                          </label>
                          <input
                            type="text"
                            name="name"
                            value={formData.name}
                            onChange={handleChange}
                            placeholder="My Awesome Project"
                            className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-violet-500/50 focus:border-violet-500 transition-all"
                          />
                        </div>

                        {/* Description */}
                        <div className="space-y-2">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <Briefcase className="w-4 h-4 text-violet-500" />
                            Description *
                          </label>
                          <textarea
                            name="description"
                            value={formData.description}
                            onChange={handleChange}
                            placeholder="Describe what your project does, the problem it solves, and key features..."
                            rows={4}
                            className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-violet-500/50 focus:border-violet-500 transition-all resize-none"
                          />
                        </div>

                        {/* Role */}
                        <div className="space-y-2">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <Briefcase className="w-4 h-4 text-violet-500" />
                            Your Role
                          </label>
                          <input
                            type="text"
                            name="role"
                            value={formData.role || ''}
                            onChange={handleChange}
                            placeholder="e.g., Lead Developer, Designer, etc."
                            className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-violet-500/50 focus:border-violet-500 transition-all"
                          />
                        </div>

                        {/* Tech Stack */}
                        <div className="space-y-2">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <Code2 className="w-4 h-4 text-violet-500" />
                            Tech Stack
                          </label>
                          <div className="flex gap-2">
                            <input
                              type="text"
                              value={techInput}
                              onChange={(e) => setTechInput(e.target.value)}
                              onKeyPress={handleKeyPress}
                              placeholder="Add technology (press Enter)"
                              className="flex-1 px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-violet-500/50 focus:border-violet-500 transition-all"
                            />
                            <Button
                              type="button"
                              onClick={addTech}
                              className="px-4 bg-violet-500 hover:bg-violet-600 text-white rounded-xl"
                            >
                              <Plus className="w-5 h-5" />
                            </Button>
                          </div>
                          {/* Tech Tags */}
                          {formData.techStack && formData.techStack.length > 0 && (
                            <motion.div
                              initial={{ opacity: 0 }}
                              animate={{ opacity: 1 }}
                              className="flex flex-wrap gap-2 mt-3"
                            >
                              {formData.techStack.map((tech) => (
                                <motion.span
                                  key={tech}
                                  initial={{ scale: 0 }}
                                  animate={{ scale: 1 }}
                                  exit={{ scale: 0 }}
                                  className="inline-flex items-center gap-1.5 px-3 py-1.5 bg-gradient-to-r from-violet-500/10 to-fuchsia-500/10 border border-violet-500/20 text-violet-600 dark:text-violet-400 rounded-lg text-sm font-medium"
                                >
                                  {tech}
                                  <button
                                    type="button"
                                    onClick={() => removeTech(tech)}
                                    className="hover:text-red-500 transition-colors"
                                  >
                                    <X className="w-3.5 h-3.5" />
                                  </button>
                                </motion.span>
                              ))}
                            </motion.div>
                          )}
                        </div>

                        {/* Date Range */}
                        <div className="grid grid-cols-2 gap-4">
                          <div className="space-y-2">
                            <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                              <Calendar className="w-4 h-4 text-violet-500" />
                              Start Date *
                            </label>
                            <input
                              type="date"
                              name="startDate"
                              value={formData.startDate}
                              onChange={handleChange}
                              className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white focus:outline-none focus:ring-2 focus:ring-violet-500/50 focus:border-violet-500 transition-all"
                            />
                          </div>
                          <div className="space-y-2">
                            <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                              <Calendar className="w-4 h-4 text-violet-500" />
                              End Date
                            </label>
                            <input
                              type="date"
                              name="endDate"
                              value={formData.endDate || ''}
                              onChange={handleChange}
                              disabled={formData.isCurrent}
                              className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white focus:outline-none focus:ring-2 focus:ring-violet-500/50 focus:border-violet-500 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
                            />
                          </div>
                        </div>

                        {/* Currently Working */}
                        <label className="flex items-center gap-3 cursor-pointer group">
                          <div className="relative">
                            <input
                              type="checkbox"
                              name="isCurrent"
                              checked={formData.isCurrent}
                              onChange={handleCheckboxChange}
                              className="sr-only peer"
                            />
                            <div className="w-5 h-5 border-2 border-gray-300 dark:border-neutral-600 rounded-md peer-checked:bg-violet-500 peer-checked:border-violet-500 transition-all flex items-center justify-center">
                              {formData.isCurrent && (
                                <svg className="w-3 h-3 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={3}>
                                  <path strokeLinecap="round" strokeLinejoin="round" d="M5 13l4 4L19 7" />
                                </svg>
                              )}
                            </div>
                          </div>
                          <span className="text-sm text-gray-700 dark:text-neutral-300 group-hover:text-violet-500 transition-colors">
                            I&apos;m currently working on this project
                          </span>
                        </label>

                        {/* Links Section */}
                        <div className="pt-4 border-t border-gray-200/50 dark:border-neutral-800/50">
                          <h3 className="flex items-center gap-2 text-sm font-semibold text-gray-900 dark:text-white mb-4">
                            <Link2 className="w-4 h-4 text-violet-500" />
                            Project Links
                          </h3>

                          <div className="space-y-4">
                            {/* Live URL */}
                            <div className="space-y-2">
                              <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                                <Globe className="w-4 h-4 text-emerald-500" />
                                Live Demo URL
                              </label>
                              <input
                                type="url"
                                name="projectUrl"
                                value={formData.projectUrl || ''}
                                onChange={handleChange}
                                placeholder="https://myproject.com"
                                className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-emerald-500/50 focus:border-emerald-500 transition-all"
                              />
                            </div>

                            {/* GitHub URL */}
                            <div className="space-y-2">
                              <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                                <Github className="w-4 h-4 text-gray-700 dark:text-neutral-300" />
                                GitHub Repository
                              </label>
                              <input
                                type="url"
                                name="githubUrl"
                                value={formData.githubUrl || ''}
                                onChange={handleChange}
                                placeholder="https://github.com/username/repo"
                                className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-gray-500/50 focus:border-gray-500 transition-all"
                              />
                            </div>
                          </div>
                        </div>

                        {/* Featured Toggle */}
                        <div className="pt-4 border-t border-gray-200/50 dark:border-neutral-800/50">
                          <label className="flex items-center justify-between cursor-pointer group p-4 bg-gradient-to-r from-amber-500/5 to-orange-500/5 border border-amber-500/20 rounded-xl">
                            <div className="flex items-center gap-3">
                              <Star className="w-5 h-5 text-amber-500" />
                              <div>
                                <span className="block text-sm font-medium text-gray-900 dark:text-white">
                                  Feature this project
                                </span>
                                <span className="block text-xs text-gray-500 dark:text-neutral-400 mt-0.5">
                                  Featured projects appear at the top of your profile
                                </span>
                              </div>
                            </div>
                            <div className="relative">
                              <input
                                type="checkbox"
                                name="featured"
                                checked={formData.featured}
                                onChange={handleCheckboxChange}
                                className="sr-only peer"
                              />
                              <div className="w-11 h-6 bg-gray-200 dark:bg-neutral-700 rounded-full peer-checked:bg-amber-500 transition-colors" />
                              <div className="absolute top-0.5 left-0.5 w-5 h-5 bg-white rounded-full shadow-md transform peer-checked:translate-x-5 transition-transform" />
                            </div>
                          </label>
                        </div>
                      </div>

                      {/* Footer */}
                      <div className="flex items-center justify-end gap-3 p-5 border-t border-gray-200/50 dark:border-neutral-800/50 bg-gray-50/50 dark:bg-neutral-800/30">
                        <Button
                          type="button"
                          onClick={onClose}
                          className="px-5 py-2.5 bg-gray-200 dark:bg-neutral-700 hover:bg-gray-300 dark:hover:bg-neutral-600 text-gray-700 dark:text-neutral-300 rounded-xl transition-colors"
                        >
                          Cancel
                        </Button>
                        <Button
                          type="submit"
                          disabled={saving}
                          className="px-6 py-2.5 bg-gradient-to-r from-violet-500 to-fuchsia-600 hover:from-violet-600 hover:to-fuchsia-700 text-white rounded-xl shadow-lg shadow-violet-500/25 disabled:opacity-50 disabled:cursor-not-allowed transition-all"
                        >
                          {saving ? (
                            <>
                              <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                              Adding...
                            </>
                          ) : (
                            <>
                              <Plus className="w-4 h-4 mr-2" />
                              Add Project
                            </>
                          )}
                        </Button>
                      </div>
                    </form>
                  </div>
                </motion.div>
              </Dialog.Content>
            </>
          )}
        </AnimatePresence>
      </Dialog.Portal>
    </Dialog.Root>
  );
}
